/*! Fonts */
/*! Fonts End */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  font: inherit;
  font-size: 100%;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

a {
  text-decoration: none;
}

/*! Nulling styles */
*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

input, input:focus {
  outline: none;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

a, button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

img {
  vertical-align: top;
}

html, body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

/*! Nulling styles End */
/*! Defaults */
html, body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  width: 100%;
  height: 100%;
}

body {
  background-color: white;
  color: #313131;
  letter-spacing: -.022em;
  font-weight: 400;
  font-style: normal;
  font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  line-height: 1.47059;
}

@media (max-width: 767px) {
  body {
    font-size: calc(12px + 8.5 * ((100vw - 375px) / 1920));
  }
}

@media (min-width: 767px) {
  body {
    font-size: calc(12px + 5 * (100vw / 1920));
  }
}

.wrapper {
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
}

.header {
  flex: 0 0 auto;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
  width: 100%;
}

@media screen and (max-width: 1199.98px) {
  .container {
    max-width: calc(1199.98px - 30px);
  }
}

@media screen and (max-width: 991.98px) {
  .container {
    max-width: calc(991.98 px - 30px);
  }
}

@media screen and (max-width: 767.98px) {
  .container {
    max-width: calc(767.98px - 30px);
  }
}

@media screen and (max-width: 575.98px) {
  .container {
    max-width: calc(575.98px - 30px);
  }
}

.img-wrap {
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  height: auto;
}

.img-wrap img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-wrap picture {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*! Defaults End */
/*! .header */
/*! .header end*/
/*! .footer */
/*! .footer end*/
@media screen and (max-width: 1199px) {
  .header, .footer {
    background: #ff0022;
  }
}

@media screen and (max-width: 991px) {
  .header, .footer {
    background: #fbff00;
  }
}

@media screen and (max-width: 767px) {
  .header, .footer {
    background: #2bff00;
  }
}

@media screen and (max-width: 575px) {
  .header, .footer {
    background: #00ccff;
  }
}

@media screen and (max-width: 319px) {
  .header, .footer {
    background: #ff0080;
  }
}
